<template>
  <div class="notice">
    <crumb></crumb>
    <div class="table">
      <el-table
        :data="data"
        style="width: 100%">
        <el-table-column
          label="通知公告">
          <template slot-scope="scope">
            <el-button type="text">{{scope.row.title}}</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="date"
          label="发布时间" >
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'notice',
  data() {
    return {
      data: [
        {
          title: '认证申请提交说明',
          date: '2019-10-17'
        },
        {
          title: '认证培训通告',
          date: '2019-09-30'
        },
        {
          title: '关于接受2020年工程教育认证申请的通告',
          date: '2019-09-16'
        },
        {
          title: '关于参加工程教育认证有关事宜的通告',
          date: '2019-09-05'
        },
        {
          title: '关于发布已通过工程教育认证专业名单的通告（截至2018年）',
          date: '2019-06-24'
        },
        {
          title: '关于举办“构建和完善内外联动的高校质量保障体系” 第二期宣讲通知',
          date: '2019-10-12'
        },
        {
          title: '关于开展“构建和完善内外联动的高校质量保障体系” 第一期宣讲通知',
          date: '2019-09-09'
        },
        {
          title: '关于开展“以本为本创新地方本科院校应用型人才培养模式”第二期通知',
          date: '2019-09-02'
        },
        {
          title: '关于开展“以本为本创新地方本科院校应用型人才培养模式”研讨培通',
          date: '2019-05-21'
        },
        {
          title: '关于开展2019年工程教育专业认证宣讲培训的通知',
          date: '2019-03-15'
        }
      ]
    }
  },
}
</script>

<style lang="scss">
.notice {
  .table {
    background-color: #fff;
    border-radius: 10px;
    padding-bottom: 50px;
  }
}
</style>


